<template>
  <div class="changePassword">
    <div class="record-bar">
      <van-nav-bar
        :title="$t('ModifyLoginPassword')"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="title">{{$t('tips')}}</div>
    <div class="content new-content">
      <van-cell-group>
        <van-field type="password" v-model="newPass" :label="$t('oldPass')" :placeholder="$t('oldPassQ')" />
        <van-field type="password" v-model="confirmNewPass" :label="$t('newPass')" :placeholder="$t('newPassQ')"/>
        <van-field type="password" v-model="confirmPass" :label="$t('newPassC')" :placeholder="$t('newPassR')"/>
      </van-cell-group>
    </div>
    <div class="button" @click="linkAccountManage">{{$t('Confirm')}}</div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
export default {
  name: 'newPassword',
  data () {
    return {
      newPass: '',
      confirmNewPass: '',
      confirmPass: '',
      // 盘口类型 盘口URL
      discUrl:''
    }
  },
  created(){
    console.log(this.$store.state.environment)
    console.log(this.$store.state.discUrl)
    console.log(this.$route.params.phone)
    this.discUrl = this.$store.state.discUrl
    // this.discUrl = '47.74.187.199'
  },
  methods: {
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    },
    // 返回账户管理
    // linkAccountManage () {
    //    this.$axios.get('http://'+this.discUrl+':7777/api/user/repassword?phone='+this.$route.params.phone+'&password='+this.newPass+'&keying='+this.$route.params.keying+'&code='+this.$route.params.code).then(res => {
    //      console.log(res)
    //      if(res.data.code === 0){
    //        let _this = this
    //        if(_this.$route.params.fromOff) {
    //          console.log(_this.$route.params.fromOff)
    //          Toast.success({
    //            message: '修改登录密码成功,请重新登录',
    //            position: 'bottom',
    //            duration: 1000,
    //            onClose() {
    //              _this.Logout()
    //            }
    //          })
    //        }else {
    //          Toast.success({
    //            message: '修改登录密码成功',
    //            position: 'bottom',
    //            duration: 1000,
    //            onClose() {
    //              _this.$router.push({
    //                name: 'signIn'
    //              })
    //            }
    //          })
    //        }
    //      }else {
    //        Toast({
    //          duration:1000,
    //          message:res.data.msg,
    //          position:'bottom'
    //        })
    //      }
    //    })
    //  }
    linkAccountManage(){
      let that = this
      if(this.confirmPass === this.confirmNewPass){
        if (that.global.ws && that.global.ws.readyState === 1) {
          this.global.ws.onopen = this.setNoEntrust()
        }
        that.global.ws.onmessage = function(res) {
          that.getNoEntrust (res)
        };
      }else {
        Toast.success({
          message: that.$t('pwyz'),
          duration: 2000,
          position:'bottom',
          onClose() {
            that.confirmPass = ''
            that.confirmNewPass = ''
          }
        })
      }

    },
    setNoEntrust(){
      let actions;
      let that = this
      actions = {
        action: "repassword",
        reqID: 754,
        data: {
          old : that.newPass,
          now : that.confirmNewPass,
        },
      }
      that.global.ws.send(JSON.stringify(actions))
    },
    getNoEntrust(e){
      this.redata = JSON.parse(e.data)
      let that = this
      console.log(this.redata)
      if(this.redata.action === "repassword" ){
        if(this.redata.data.code === 0){
          Toast.success({
            message: that.$t('pwts'),
            duration: 2000,
            onClose() {
              that.Logout()
            }
          })
        }else {
          Toast({
            message: that.$t('pwts2'),
            position:'bottom',
            duration: 2000,
          })
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



